/**
 * 粒子效果預設庫
 * 提供多種視覺特效預設配置：雪花、火焰、閃電、黑洞等
 */

export type ParticlePresetType = 'snowflake' | 'fire' | 'lightning' | 'blackhole' | 'explosion' | 'magic' | 'storm' | 'supernova' | 'cosmic_burst';

export interface ParticlePresetConfig {
  type: ParticlePresetType;
  colors: string[];
  behavior: ParticleBehavior;
  physics: ParticlePhysics;
  visual: ParticleVisual;
  animation: ParticleAnimation;
  spawning: ParticleSpawning;
}

export interface ParticleBehavior {
  movement: 'float' | 'rise' | 'fall' | 'spiral' | 'chain' | 'implode' | 'explode';
  gravity: number;
  drag: number;
  turbulence: number;
}

export interface ParticlePhysics {
  velocity: { min: number; max: number };
  acceleration: { x: number; y: number };
  mass: number;
  elasticity: number;
}

export interface ParticleVisual {
  size: { min: number; max: number };
  opacity: { start: number; end: number };
  scale: { start: number; end: number };
  rotation: { enabled: boolean; speed: number };
  glow: { enabled: boolean; intensity: number };
}

export interface ParticleAnimation {
  lifespan: { min: number; max: number };
  fadeIn: number;
  fadeOut: number;
  pulsing: { enabled: boolean; frequency: number };
}

export interface ParticleSpawning {
  count: { min: number; max: number };
  delay: { min: number; max: number };
  spread: { angle: number; radius: number };
  burst: boolean;
}

