/**
 * 根據遊戲等級獲取建議的粒子效果
 */
export function getPresetForGrade(grade: string): ParticlePresetType {
  switch (grade) {
    case 'SSS':
      return 'cosmic_burst';
    case 'SS':
      return 'supernova';
    case 'S':
      return 'fire';
    case 'A':
      return 'explosion';
    case 'B':
      return 'magic';
    case 'C':
    default:
      return 'snowflake';
  }
}

/**
 * 根據特殊情況獲取粒子效果
 */
export function getPresetForSpecialMove(type: 'corner' | 'massive' | 'comeback' | 'domination' | 'combo'): ParticlePresetType {
  switch (type) {
    case 'corner':
      return 'explosion';
    case 'massive':
      return 'storm';
    case 'comeback':
      return 'fire';
    case 'domination':
      return 'blackhole';
    case 'combo':
      return 'lightning';
    default:
      return 'magic';
  }
}

/**
 * 混合兩種預設效果
 */
export function blendPresets(preset1: ParticlePresetType, preset2: ParticlePresetType, ratio: number = 0.5): ParticlePresetConfig {
  const config1 = PARTICLE_PRESETS[preset1];
  const config2 = PARTICLE_PRESETS[preset2];

  return {
    type: preset1,
    colors: [...config1.colors, ...config2.colors],
    behavior: {
      movement: ratio > 0.5 ? config1.behavior.movement : config2.behavior.movement,
      gravity: config1.behavior.gravity * ratio + config2.behavior.gravity * (1 - ratio),
      drag: config1.behavior.drag * ratio + config2.behavior.drag * (1 - ratio),
      turbulence: config1.behavior.turbulence * ratio + config2.behavior.turbulence * (1 - ratio)
    },
    physics: {
      velocity: {
        min: Math.min(config1.physics.velocity.min, config2.physics.velocity.min),
        max: Math.max(config1.physics.velocity.max, config2.physics.velocity.max)
      },
      acceleration: {
        x: config1.physics.acceleration.x * ratio + config2.physics.acceleration.x * (1 - ratio),
        y: config1.physics.acceleration.y * ratio + config2.physics.acceleration.y * (1 - ratio)
      },
      mass: config1.physics.mass * ratio + config2.physics.mass * (1 - ratio),
      elasticity: config1.physics.elasticity * ratio + config2.physics.elasticity * (1 - ratio)
    },
    visual: {
      size: {
        min: Math.min(config1.visual.size.min, config2.visual.size.min),
        max: Math.max(config1.visual.size.max, config2.visual.size.max)
      },
      opacity: {
        start: Math.max(config1.visual.opacity.start, config2.visual.opacity.start),
        end: Math.min(config1.visual.opacity.end, config2.visual.opacity.end)
      },
      scale: {
        start: config1.visual.scale.start * ratio + config2.visual.scale.start * (1 - ratio),
        end: config1.visual.scale.end * ratio + config2.visual.scale.end * (1 - ratio)
      },
      rotation: {
        enabled: config1.visual.rotation.enabled || config2.visual.rotation.enabled,
        speed: config1.visual.rotation.speed * ratio + config2.visual.rotation.speed * (1 - ratio)
      },
      glow: {
        enabled: config1.visual.glow.enabled || config2.visual.glow.enabled,
        intensity: Math.max(config1.visual.glow.intensity, config2.visual.glow.intensity)
      }
    },
    animation: {
      lifespan: {
        min: Math.min(config1.animation.lifespan.min, config2.animation.lifespan.min),
        max: Math.max(config1.animation.lifespan.max, config2.animation.lifespan.max)
      },
      fadeIn: Math.max(config1.animation.fadeIn, config2.animation.fadeIn),
      fadeOut: Math.max(config1.animation.fadeOut, config2.animation.fadeOut),
      pulsing: {
        enabled: config1.animation.pulsing.enabled || config2.animation.pulsing.enabled,
        frequency: config1.animation.pulsing.frequency * ratio + config2.animation.pulsing.frequency * (1 - ratio)
      }
    },
    spawning: {
      count: {
        min: Math.max(config1.spawning.count.min, config2.spawning.count.min),
        max: Math.max(config1.spawning.count.max, config2.spawning.count.max)
      },
      delay: {
        min: Math.min(config1.spawning.delay.min, config2.spawning.delay.min),
        max: Math.max(config1.spawning.delay.max, config2.spawning.delay.max)
      },
      spread: {
        angle: Math.max(config1.spawning.spread.angle, config2.spawning.spread.angle),
        radius: Math.max(config1.spawning.spread.radius, config2.spawning.spread.radius)
      },
      burst: config1.spawning.burst || config2.spawning.burst
    }
  };
}