  // Create flip animation particles - SPECTACULAR ENHANCED!
  createFlipEffect(x: number, y: number, color: string, grade?: Grade): void {
    // Much more spectacular flip effects with grade-based colors!
    const isBlack = color === '#000000';
    
    // Get grade-based colors or fallback to basic colors
    let gradeColors: string[];
    let intensity: number;
    
    if (grade) {
      switch (grade) {
        case 'SSS':
          // Use cosmic_burst colors - golden theme
          gradeColors = ['#ffd700', '#ffef94', '#fff68f', '#ffffe0', '#fffacd'];
          intensity = 3.0;
          break;
        case 'SS':
          // Use supernova colors - orange theme
          gradeColors = ['#ff8c00', '#ffa500', '#ff7f00', '#ff6347', '#ff4500'];
          intensity = 2.5;
          break;
        case 'S':
          // Use light purple theme - 淺紫色系主題
          gradeColors = ['#dda0dd', '#e6b8ea', '#d8bfd8', '#e0b0ff', '#dbb2ff'];
          intensity = 2.0;
          break;
        case 'A':
          // Use light blue theme - 淺藍色系主題
          gradeColors = ['#87ceeb', '#add8e6', '#b0e0e6', '#afeeee', '#e0ffff'];
          intensity = 1.5;
          break;
        case 'B':
          // Use light green theme - 淺綠色系主題
          gradeColors = ['#90ee90', '#98fb98', '#b4ffb4', '#c8ffc8', '#a8e6a8'];
          intensity = 1.2;
          break;
        case 'C':
          // Use white/gray theme - 白灰色系主題
          gradeColors = ['#d3d3d3', '#c0c0c0', '#e0e0e0', '#f5f5f5', '#b8b8b8'];
          intensity = 1.0;
          break;
        default:
          gradeColors = isBlack ? ['#666666', '#888888', '#555555'] : ['#f0f0f0', '#e8e8e8', '#f5f5f5'];
          intensity = 0.8;
      }
    } else {
      gradeColors = isBlack ? ['#666666', '#888888', '#555555'] : ['#f0f0f0', '#e8e8e8', '#f5f5f5'];
      intensity = 0.8;
    }
    
    const primaryColor = gradeColors[0] ?? '#ffffff';
    const accentColors = gradeColors;
    
    console.log(`🔄 Flip Effect: ${isBlack ? 'Black' : 'White'} piece flipped! Grade: ${grade || 'Basic'}`);
    
    // EXPLOSIVE FLIP BURST - Intensity based on grade!
    const burstCount = Math.floor(15 * intensity);
    for (let i = 0; i < burstCount; i++) {
      const angle = (i / burstCount) * Math.PI * 2;
      const speed = (Math.random() * 60 + 60) * intensity;
      
      this.particles.push({
        x: x,
        y: y,
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: 0.6 * intensity,
        maxLife: 0.6 * intensity,
        size: (Math.random() * 1.5 + 1) * intensity,
        color: i % 3 === 0 ? primaryColor : accentColors[i % accentColors.length] ?? primaryColor,
        alpha: 0.9,
        type: i % 2 === 0 ? 'spark' : 'circle'
      });
    }
    
    // RING EXPANSION - Visual flip wave (intensity-based)
    const ringCount = Math.floor(2 + intensity);
    for (let ring = 0; ring < ringCount; ring++) {
      setTimeout(() => {
        const particlesPerRing = Math.floor(12 + intensity * 4);
        for (let i = 0; i < particlesPerRing; i++) {
          const angle = (i / particlesPerRing) * Math.PI * 2;
          const speed = (80 + ring * 30) * intensity;
          
          this.particles.push({
            x: x,
            y: y,
            vx: Math.cos(angle) * speed,
            vy: Math.sin(angle) * speed,
            life: 0.6 + ring * 0.2,
            maxLife: 0.6 + ring * 0.2,
            size: ring === 0 ? 2 : 1,
            color: primaryColor,
            alpha: 0.7 - ring * 0.2,
            type: 'circle'
          });
        }
      }, ring * 80);
    }
    
    // FLIP SPARKLE TRAIL - Cascading sparkles
    for (let i = 0; i < 15; i++) {
      const angle = Math.random() * Math.PI * 2;
      const speed = Math.random() * 60 + 40;
      
      setTimeout(() => {
        this.particles.push({
          x: x + (Math.random() - 0.5) * 20,
          y: y + (Math.random() - 0.5) * 20,
          vx: Math.cos(angle) * speed,
          vy: Math.sin(angle) * speed,
          life: 0.7,
          maxLife: 0.7,
          size: Math.random() * 1.5 + 1,
          color: accentColors[Math.floor(Math.random() * accentColors.length)] ?? '#d3d3d3',
          alpha: 0.8,
          type: 'spark'
        });
      }, Math.random() * 200);
    }
    
    // MAGIC DUST - Floating ambient particles
    for (let i = 0; i < 8; i++) {
      setTimeout(() => {
        this.particles.push({
          x: x + (Math.random() - 0.5) * 30,
          y: y + (Math.random() - 0.5) * 30,
          vx: (Math.random() - 0.5) * 30,
          vy: -Math.random() * 40 - 20,
          life: 1.2,
          maxLife: 1.2,
          size: Math.random() * 1 + 0.5,
          color: primaryColor,
          alpha: 0.6,
          type: 'circle'
        });
      }, Math.random() * 300);
    }
    
    // FLIP SHOCKWAVE - Single expanding ring
    setTimeout(() => {
      for (let angle = 0; angle < Math.PI * 2; angle += 0.2) {
        this.particles.push({
          x: x,
          y: y,
          vx: Math.cos(angle) * 150,
          vy: Math.sin(angle) * 150,
          life: 0.4,
          maxLife: 0.4,
          size: 1.5,
          color: isBlack ? '#333333' : '#f9f9f9',
          alpha: 0.5,
          type: 'circle'
        });
      }
    }, 100);
  }
  
  // Clean up
  destroy(): void {
    if (this.animationId) {
      cancelAnimationFrame(this.animationId);
    }
    this.particles = [];
    this.canvas.remove();
  }
  
  // Get particle count for performance monitoring
  getParticleCount(): number {
    return this.particles.length;
  }
  
  // Special situation effects
  private createCornerExplosion(x: number, y: number): void {
    // Golden castle walls effect for corner captures
    const castleColors = ['#ffd700', '#ffdf00', '#ffa500'];
    
    // Castle wall pattern
    for (let wall = 0; wall < 4; wall++) {
      const angle = (wall / 4) * Math.PI * 2;
      
      for (let brick = 0; brick < 20; brick++) {
        setTimeout(() => {
          this.particles.push({
            x: x,
            y: y,
            vx: Math.cos(angle) * 300 + (Math.random() - 0.5) * 100,
            vy: Math.sin(angle) * 300 + (Math.random() - 0.5) * 100,
            life: 1.5,
            maxLife: 1.5,
            size: Math.random() * 3 + 2,
            color: castleColors[brick % castleColors.length] ?? '#ffd700',
            alpha: 1,
            type: 'circle'
          });
        }, brick * 30 + wall * 200);
      }
    }
  }
  
