/**
 * 連擊系統
 * 追蹤玩家連續下棋狀態並觸發相應特效
 */

import type { PlayerColor } from '@/types/game-types';
import { ParticlePresetType, getPresetForSpecialMove, blendPresets, PARTICLE_PRESETS } from './particle-presets';
import { t } from '@/i18n';

export interface ComboState {
  /** 連擊次數 */
  count: number;
  /** 當前連擊玩家 */
  player: PlayerColor | null;
  /** 連擊倍數 */
  multiplier: number;
  /** 連擊開始時間 */
  startTime: number;
  /** 是否處於連擊狀態 */
  isActive: boolean;
  /** 最高連擊記錄 */
  maxCombo: number;
  /** 連擊類型 */
  type: ComboType;
}

export type ComboType =
  | 'none'
  | 'double'
  | 'triple'
  | 'quadruple'
  | 'quintuple'
  | 'sextuple'
  | 'septuple';

export interface ComboEffect {
  /** 特效類型 */
  effectType: ParticlePresetType;
  /** 強度倍數 */
  intensity: number;
  /** 持續時間 */
  duration: number;
  /** 特殊音效 */
  soundEffect?: string;
  /** 螢幕震動 */
  screenShake?: boolean;
  /** 慢動作效果 */
  slowMotion?: boolean;
}

export interface ComboEvent {
  type: 'combo_started' | 'combo_continued' | 'combo_ended' | 'combo_broken';
  player: PlayerColor;
  comboCount: number;
  comboType: ComboType;
  effect?: ComboEffect;
  timestamp: number;
}

/**
 * 連擊等級配置
 */
export const COMBO_LEVELS: Record<ComboType, ComboEffect> = {
  none: {
    effectType: 'snowflake',
    intensity: 1.0,
    duration: 500,
  },
  double: {
    effectType: 'magic',
    intensity: 1.5,
    duration: 800,
    soundEffect: 'combo_double',
  },
  triple: {
    effectType: 'fire',
    intensity: 2.0,
    duration: 1200,
    soundEffect: 'combo_triple',
    screenShake: true,
  },
  quadruple: {
    effectType: 'explosion',
    intensity: 2.5,
    duration: 1500,
    soundEffect: 'combo_quadruple',
    screenShake: true,
  },
  quintuple: {
    effectType: 'lightning',
    intensity: 3.0,
    duration: 1800,
    soundEffect: 'combo_quintuple',
    screenShake: true,
    slowMotion: false,
  },
  sextuple: {
    effectType: 'storm',
    intensity: 4.0,
    duration: 2200,
    soundEffect: 'combo_sextuple',
    screenShake: true,
    slowMotion: true,
  },
  septuple: {
    effectType: 'blackhole',
    intensity: 5.0,
    duration: 2500,
    soundEffect: 'combo_septuple',
    screenShake: true,
    slowMotion: true,
  }
};

/**
 * 連擊系統主類
 */
export class ComboSystem {
  private _state: ComboState;
  private _eventHandlers: ((event: ComboEvent) => void)[] = [];
  private _history: ComboEvent[] = [];

  constructor() {
    this._state = this.createInitialState();
  }

  /**
   * 創建初始狀態
   */
  private createInitialState(): ComboState {
    return {
      count: 0,
      player: null,
      multiplier: 1.0,
      startTime: 0,
      isActive: false,
      maxCombo: 0,
      type: 'none'
    };
  }

  /**
   * 獲取當前連擊狀態
   */
  get state(): Readonly<ComboState> {
    return Object.freeze({ ...this._state });
  }

  /**
   * 獲取連擊歷史
   */
  get history(): readonly ComboEvent[] {
    return Object.freeze([...this._history]);
  }

  /**
   * 處理玩家移動
   * @param player 當前玩家
   * @param opponentCanMove 對手是否能移動
   * @param moveScore 當前移動分數
   */
  processMove(player: PlayerColor, opponentCanMove: boolean, moveScore?: number): ComboEvent | null {
    console.log(`🔥 Combo: 處理移動 - Player: ${player}, 對手可移動: ${opponentCanMove}, 分數: ${moveScore}`);

    // 如果對手能移動，重置連擊
    if (opponentCanMove) {
      console.log('🔥 Combo: 對手可移動，結束連擊');
      return this.endCombo();
    } else {
      console.log('🔥 Combo: 對手無法移動，繼續連擊！');
    }

    // 檢查是否是同一玩家繼續連擊
    if (this._state.player === player) {
      return this.continueCombo(moveScore);
    } else {
      // 新玩家開始連擊
      return this.startCombo(player, moveScore);
    }
  }

  /**
   * 開始新連擊
   */
  private startCombo(player: PlayerColor, moveScore?: number): ComboEvent {
    // 結束之前的連擊
    if (this._state.isActive) {
      this.endCombo();
    }

    this._state = {
      count: 2, // 第一次連擊至少是2回合
      player,
      multiplier: 1.2,
      startTime: Date.now(),
      isActive: true,
      maxCombo: Math.max(this._state.maxCombo, 2),
      type: 'double'
    };

    const event: ComboEvent = {
      type: 'combo_started',
      player,
      comboCount: this._state.count,
      comboType: this._state.type,
      effect: this.getComboEffect(),
      timestamp: Date.now()
    };

    this._emitEvent(event);
    this._history.push(event);

    return event;
  }

  /**
   * 繼續連擊
   */
  private continueCombo(moveScore?: number): ComboEvent {
    if (!this._state.isActive || !this._state.player) {
      throw new Error('Cannot continue combo: no active combo');
    }

    this._state.count++;
    this._state.multiplier = Math.min(1.0 + (this._state.count * 0.2), 3.0);
    this._state.maxCombo = Math.max(this._state.maxCombo, this._state.count);
    this._state.type = this.determineComboType(this._state.count);

    const event: ComboEvent = {
      type: 'combo_continued',
      player: this._state.player,
      comboCount: this._state.count,
      comboType: this._state.type,
      effect: this.getComboEffect(),
      timestamp: Date.now()
    };

    this._emitEvent(event);
    this._history.push(event);

    return event;
  }

  /**
   * 結束連擊
   */
  private endCombo(): ComboEvent | null {
    if (!this._state.isActive || !this._state.player) {
      return null;
    }

    const event: ComboEvent = {
      type: 'combo_ended',
      player: this._state.player,
      comboCount: this._state.count,
      comboType: this._state.type,
      timestamp: Date.now()
    };

    // 重置狀態
    this._state = this.createInitialState();
    this._state.maxCombo = this.state.maxCombo; // 保持最高記錄

    this._emitEvent(event);
    this._history.push(event);

    return event;
  }

  /**
   * 強制中斷連擊
   */
  breakCombo(reason: string = 'manual'): ComboEvent | null {
    if (!this._state.isActive || !this._state.player) {
      return null;
    }

    const event: ComboEvent = {
      type: 'combo_broken',
      player: this._state.player,
      comboCount: this._state.count,
      comboType: this._state.type,
      timestamp: Date.now()
    };

    this._state = this.createInitialState();
    this._state.maxCombo = this.state.maxCombo;

    this._emitEvent(event);
    this._history.push(event);

    return event;
  }

  /**
   * 判斷連擊類型
   */
  private determineComboType(count: number): ComboType {
    if (count >= 7) return 'septuple';
    if (count >= 6) return 'sextuple';
    if (count >= 5) return 'quintuple';
    if (count >= 4) return 'quadruple';
    if (count >= 3) return 'triple';
    if (count >= 2) return 'double';
    return 'none';
  }

  /**
   * 獲取連擊特效配置
   */
  private getComboEffect(): ComboEffect {
    const baseEffect = COMBO_LEVELS[this._state.type];

    // 根據連擊次數調整強度
    const intensityBonus = Math.min(this._state.count * 0.1, 1.0);

    return {
      ...baseEffect,
      intensity: baseEffect.intensity + intensityBonus,
      duration: baseEffect.duration + (this._state.count * 50)
    };
  }

  /**
   * 獲取連擊統計
   */
  getStats(): {
    currentCombo: number;
    maxCombo: number;
    totalCombos: number;
    averageComboLength: number;
    comboTypeDistribution: Record<ComboType, number>;
  } {
    const combos = this._history.filter(e => e.type === 'combo_ended' || e.type === 'combo_broken');
    const totalCombos = combos.length;
    const averageLength = totalCombos > 0
      ? combos.reduce((sum, combo) => sum + combo.comboCount, 0) / totalCombos
      : 0;

    const distribution: Record<ComboType, number> = {
      none: 0,
      double: 0,
      triple: 0,
      quadruple: 0,
      quintuple: 0,
      sextuple: 0,
      septuple: 0
    };

    combos.forEach(combo => {
      distribution[combo.comboType]++;
    });

    return {
      currentCombo: this._state.count,
      maxCombo: this._state.maxCombo,
      totalCombos,
      averageComboLength: averageLength,
      comboTypeDistribution: distribution
    };
  }

  /**
   * 獲取連擊建議特效
   */
  getSuggestedEffect(comboType: ComboType): ParticlePresetType {
    switch (comboType) {
      case 'septuple':
        return 'blackhole';
      case 'sextuple':
        return 'storm';
      case 'quintuple':
        return 'lightning';
      case 'quadruple':
        return 'explosion';
      case 'triple':
        return 'fire';
      case 'double':
        return 'magic';
      default:
        return 'snowflake';
    }
  }

  /**
   * 檢查是否應該播放特殊音效
   */
  shouldPlaySpecialSound(comboType: ComboType): boolean {
    return !['none', 'double'].includes(comboType);
  }

  /**
   * 檢查是否應該觸發螢幕震動
   */
  shouldTriggerScreenShake(comboType: ComboType): boolean {
    return ['quintuple', 'sextuple', 'septuple'].includes(comboType);
  }

  /**
   * 檢查是否應該觸發慢動作效果
   */
  shouldTriggerSlowMotion(comboType: ComboType): boolean {
    return comboType === 'septuple';
  }

  /**
   * 重置系統
   */
  reset(): void {
    this._state = this.createInitialState();
    this._history = [];
  }

  /**
   * 註冊事件處理器
   */
  addEventListener(handler: (event: ComboEvent) => void): void {
    this._eventHandlers.push(handler);
  }

  /**
   * 移除事件處理器
   */
  removeEventListener(handler: (event: ComboEvent) => void): void {
    const index = this._eventHandlers.indexOf(handler);
    if (index !== -1) {
      this._eventHandlers.splice(index, 1);
    }
  }

  /**
   * 發送事件
   */
  private _emitEvent(event: ComboEvent): void {
    this._eventHandlers.forEach(handler => {
      try {
        handler(event);
      } catch (error) {
        console.error('Combo event handler error:', error);
      }
    });
  }

  /**
   * 獲取連擊顯示文本
   */
  getComboDisplayText(): string {
    if (!this._state.isActive) return '';

    const key = `combo.${this._state.type}`;
    const text = t(key);
    return text === key ? `${this._state.count} combo!` : text;
  }

  /**
   * 獲取連擊顏色
   */
  getComboDisplayColor(): string {
    const colors = {
      none: '#ffffff',
      double: '#90ee90',
      triple: '#ffa500',
      quadruple: '#ff6347',
      quintuple: '#ff1493',
      sextuple: '#9932cc',
      septuple: '#ffd700'
    };

    return colors[this._state.type as keyof typeof colors] || '#ffffff';
  }
}