/**
 * 必殺技特效系統
 * 處理角落支配、大規模翻轉、完美遊戲、逆轉等特殊招式
 */

import type { PlayerColor, BoardPosition } from '@/types/game-types';
import { ParticlePresetType, ParticlePresetConfig, PARTICLE_PRESETS, blendPresets } from './particle-presets';
import { t } from '@/i18n';

export type SpecialMoveType =
  | 'corner_joseki'    // 角部定式 - 经典角部布局
  | 'great_reversal'   // 大翻盘 - 乾坤大挪移，局势逆转
  | 'total_domination' // 完全压制 - 如臂使指，掌控全局
  | 'desperate_kill'   // 绝地反杀 - 绝境求生，反败为胜
  | 'perfect_endgame'  // 完美收官 - 收官如神，无懈可击
  | 'edge_control'     // 边线掌控 - 边线为王，稳扎稳打
  | 'chain_reaction'   // 连锁反应 - 环环相扣，精妙计算
  | 'time_control';    // 时间掌控 - 运筹帷幄，决胜千里

export interface SpecialMoveCondition {
  /** 翻轉棋子數量要求 */
  minFlipCount?: number;
  /** 分數差距要求 */
  scoreDifference?: { min: number; max: number };
  /** 位置要求 */
  positionType?: 'corner' | 'edge' | 'center';
  /** 時機要求 */
  timing?: 'early' | 'mid' | 'late';
  /** 特殊條件 */
  special?: string;
}

export interface SpecialMoveEffect {
  /** 特效類型 */
  type: SpecialMoveType;
  /** 粒子效果配置 */
  particleEffect: ParticlePresetConfig;
  /** 視覺效果 */
  visual: {
    screenShake: boolean;
    slowMotion: boolean;
    zoom: boolean;
    cameraMovement: boolean;
    colorFilter?: string;
  };
  /** 音效 */
  audio: {
    soundEffect: string;
    volume: number;
    echo?: boolean;
    reverb?: boolean;
  };
  /** 持續時間 */
  duration: number;
  /** 文字顯示 */
  display: {
    title: string;
    subtitle?: string;
    color: string;
    fontSize: number;
    animation: 'fade' | 'zoom' | 'slide' | 'explode';
  };
}

